.case-details{
    padding: 200px 0 100px 0;
    @include desktop{
        padding: 120px 0 0;
    }
    .svg-img{
        position: absolute;
        top: 30%;
        left: -7%;
        animation: move_top 5s infinite linear;
        @include desktop{
            display: none;
        }
    }
    .animate-shape{
        position: absolute;
        top: 29%;
        right: 4%;
        height: 300px;
        width: 300px;
        svg{
            width: 100%;
        }
        @include desktop{
            display: none;
        }
    }
    .animate-pattern{
        position: absolute;
        top: 46%;
        right: 12%;
        height: 100px;
        width: 100px;
        animation: move_top 3s infinite linear;
        img{
            height: 100%;
            width: 100%;
        }
        @include desktop{
            display: none;
        }
    }
    &-title{
        font-size: 50px;
        line-height: 60px;
        font-family: $primary-font;
        color: $secondary-color;
        margin: 0 0 50px 0;
    }
    p{
        font-family: $secondary-font;
        color: #7E7E8A;
        font-size: 16px;
        line-height: 34px;
        margin: 0 0 30px 0;
    }
    &-info{
        display: flex;
        justify-content: space-around;
        background: $secondary-color;
        border-radius: 20px;
        padding: 65px 0;
        margin: 70px 0 80px 0;
        @include mobile{
            flex-direction: column;
            padding: 30px 0;
        }
        .case-details-info-item{
            position: relative;
            &:not(:last-child){
                &::before{
                    position: absolute;
                    content: '';
                    width: 1px;
                    height: 100%;
                    top: 0;
                    right: -80px;
                    background: rgba($color:$white, $alpha: .1);
                    @include desktop{
                        right: -45px;
                    }
                    @include tablet{
                        right: -15px;
                    }
                    @include mobile{
                        display: none;
                    }
                }
                @include mobile{
                    margin-bottom: 30px;
                }
            }
        }
        h5{
            margin: 0 0 10px 0;
            font-size: 20px;
            color: $white;
            font-family: $primary-font;
        }
        p{
            margin: 0;
        }
    }
    &-service{
        margin-bottom: 90px;
        h3{
            font-family: $primary-font;
            color: $secondary-color;
            font-size: 25px;
            margin: 0 0 25px 0;
        }
        p{
            margin: 0;
        }
    }
    &-thumb{
        margin: 0 0 90px 0;
        border-radius: 30px;
        overflow: hidden;
        img{
            height: 100%;
            width: 100%;
        }
    }
    &-row{
        display: flex;
        justify-content: center;
        gap: 1rem; // adds spacing between the images
        
        margin: 0 0 90px 0;
        img {
            max-width: 100%;
            height: auto;
            width: 45%; // adjust based on how large you want the images
            object-fit: cover;
        }
    }
    &-button{
        ul{
            margin: 0;
            padding: 0;
            li{
                list-style: none;
                display: inline-block;
                a{
                    display: block;
                    padding: 15px 30px;
                    text-decoration: none;
                    color: $secondary-color;
                    border-radius: 7px;
                    transition: all .3s ease;
                    border: 1px solid #60636F;
                    background: transparent;
                    &.active{
                        background-color: $primary-color;
                        border: 1px solid transparent;
                    }
                    &:hover{
                        border: 1px solid $primary-sec-color;
                        background: $primary-sec-color;
                        box-shadow: 0 8px 20px rgba(56, 87, 241, 0.3);
                        transform: scale(1.03);
                    }
                }
                &:not(:last-child){
                    margin-right: 10px;
                    @include mobile-s{
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
    &-popup{
        position: absolute;
        bottom: -280px;
        left: 50%;
        transform: translateX(-50%);
        transition: all .3s ease;
        @include desktop{
            bottom: -40px;
            left: 50%;
            transform: translateX(-50%);
        }
        .thumb{
            height: 320px;
            width: 500px;
            border: 1px solid #707070;
            border-radius: 20px;
            overflow: hidden;
            position: relative;
            &::before{
                position: absolute;
                content: '';
                top: 0;
                left: 0;
                height: 100%;
                width: 100%;
                background: rgba(0, 0, 0, .6);
                z-index: 1;
            }
            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
            }
        }
        .popup-button{
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            height: 60px;
            width: 60px;
            background: #ffffff;
            border-radius: 50%;
            z-index: 2;
            transition: all .3s ease;
            svg{
                position: absolute;
                top: 50%;
                left: 55%;
                transform: translate(-50%, -50%);
                transition: all .3s ease;
            }
            &:hover{
                background-color: $primary-color;
                svg{
                    path{
                        fill: #ffffff;
                    }
                }
            }
        }
        &:hover{
            .popup-button{
                transform: translate(-50%, -50%) scale(1.1);
            }
        }
    }
    &-nav{
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-top: 50px;
        .previous,.next{
            .icon{
                line-height: 0;
            }
            .content{
                span{
                    display: block;
                    font-family: $secondary-font;
                    color: #7E7E8A;
                    font-size: 15px;
                    line-height: 1;
                    margin-bottom: 5px;
                }
                h5{
                    font-family: $primary-font;
                    font-size: 20px;
                    margin: 0;
                    line-height: 1;
                    a{
                        text-decoration: none;
                        color: $secondary-color;
                        transition: all .3s ease;
                        &:hover{
                            color: $primary-color;
                        }
                    }
                }
            }
        }
        .previous{
            display: flex;
            align-items: center;
            .icon{
                margin-right: 15px;
            }
        }
        .social{
            @include mobile{
                display: none;
            }
            ul{
                margin: 0;
                padding: 0;
                li{
                    list-style: none;
                    display: inline-block;
                    a{
                        display: block;
                        text-decoration: none;
                        color: #7E7E8A;
                        font-size: 20px;
                        transition: all .3s ease;
                        &:hover{
                            color: $primary-color;
                        }
                    }
                    &:not(:last-child){
                        margin-right: 25px;
                    }
                }
            }
        }
        .next{
            display: flex;
            align-items: center;
            .icon{
                margin-left: 15px;
            }
            .content{
                text-align: right;
            }
        }
    }
}